import styled, { css } from "styled-components";

import { sizes, devices } from "theme";

const getMarginGutter = ({ theme, fullWidth }) => (fullWidth ? 0 : 1);

const getPaddingGutter = ({ theme, padding }) => (padding ? 1 : 0);

const getMaxWidthGutter = props =>
  (getMarginGutter(props) + getPaddingGutter(props)) * 2;

const breakpointsCss = Object.keys(sizes).map(
  (sizeName, idx) => css`
    @media ${devices[sizeName]} {
      max-width: calc(
        ${sizes[sizeName]}px - (var(--grid-gutter) * ${getMaxWidthGutter})
      );
    }
  `
);

const clearfixCss = css`
  &::before,
  &::after {
    content: "";
    clear: both;
    display: table;
  }
`;

export default styled.div`
  width: 100%;
  margin: 0 auto;
  padding: calc(var(--grid-gutter) * ${getPaddingGutter});
  max-width: calc(100% - var(--grid-gutter) * ${getMaxWidthGutter})
    ${({ fullWidth }) => fullWidth && "!important"};

  ${({ clearfix }) => clearfix && clearfixCss};

  ${breakpointsCss};
`;
