import { createGlobalStyle } from "styled-components";

export const sizes = {
  small: 576,
  medium: 768,
  large: 992,
  xLarge: 1200,
};

export const devices = Object.keys(sizes).reduce(
  (acc, sizeName) => ({
    ...acc,
    [sizeName]: `(min-width: ${sizes[sizeName]}px)`,
  }),
  {}
);

export const ThemeGlobalStyle = createGlobalStyle`
  body {
    --grid-gutter: 16px;

    @media ${devices.medium} {
      --grid-gutter: 32px;
    }
  }
`;

export default {};
